a {
	text-decoration: none !important;
}

.btn-body {
	height: 80px;
	width: 80px;
	border-radius: 20px;
	color: #214778;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 5px auto;
	transition: all 0.2s ease;
	background-color: rgba(244, 187, 146, 0);
}

.btn-body.active {
	background-color: rgba(244, 187, 146, 1);
	color: #fff;
	transform: scale(1.05);
}

.btn-body:hover {
	color: #fff;
}

.btn-title {
	margin: 4px auto;
	font-weight: 600;
	font-size: 14px;
}
